import Vue from "vue";
import Vuex from "vuex";
import Router from '../router'
import GlobalData from '@/utils/Constant'
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    activeKey: 0,
    panes: [{ title: "待办事项", close: true }],
    workflowActiveKey: 0,
    workflowPanes: [{ title: "工作流管理", close: false }],
    key: 0
  },
  getters: {
    activeKey: state => state.activeKey,
    panes: state => state.panes,
    workflowActiveKey: state => state.workflowActiveKey,
    workflowPanes: state => state.workflowPanes,
    key: state => state.key
  },

  actions: {
    initPane({ dispatch }, query) {
      if (Reflect.has(query, 'wftId'))
        dispatch('workflowListEditTemp', query.wftId)
      if (Reflect.has(query, 'wftId'))
        dispatch('workflowListEditTemp', query.wftId)
    },
    // 代办列表的方法
    processaddTab({ commit, state, dispatch }, wfeId) {
      console.log(wfeId);
      // dispatch('storerouterchange', wfeId);
      if (state.panes.some(v => v.id == wfeId)) {
        state.activeKey = state.panes.findIndex(v => v.id == wfeId);
      } else {
        state.panes.push({
          title: "审批处理",
          id: wfeId,
          type: 2

        });
        state.activeKey = state.panes.length - 1;
        // Router.push({
        //   path: '/dynamic',
        //   query: {
        //     wfe_id: wfeId
        //   }
        // })
      }
     console.log(state.panes,'state.panes');
    },
    storerouterchange({ commit, state }, activeKey) {
      if (activeKey == 0) {
        // Router.push({
        //   path: "/processlist",
        //   query: {
        //     open_id: "USERID"
        //   }
        // });
        state.activeKey = activeKey
      } else {
        // Router.push({
        //   path: '/dynamic',
        //   query: {
        //     wfe_id: state.panes[activeKey].id
        //   }
        // });
        state.activeKey = activeKey
      }

    },
    processremove({ commit, state }, targetKey) {
      console.log(targetKey);
      state.panes = state.panes.filter((v, i) => i != targetKey);
      state.activeKey = 0;
      console.log(state.panes);

    },
    // 工作流页面的方法
    workflowListEditTemp({ commit, state }, wftId) {
      console.warn('add')
      if (state.workflowPanes.some(v => v.id == wftId && v.title == '工作流设计')) {
        // console.log('工作流设计');
        state.workflowActiveKey = state.workflowPanes.findIndex(v => v.id == wftId && v.type == 1);
      } else {
        console.log('工作流设计');
        state.workflowPanes.push({
          title: "工作流设计",
          id: wftId,
          type: 1,
          Panesindex: state.workflowPanes.length
        });
        state.workflowActiveKey = state.workflowPanes.length - 1;
      }
    },
    workflowListdynamicAdd({ commit, state, dispatch }, wfe_id) {
      console.log(wfe_id);
      console.log(state.workflowPanes);
      if (state.workflowPanes.some(v => v.id == wfe_id && v.title == '审批动态')) {
        console.log('审批动态');
        state.workflowActiveKey = state.workflowPanes.findIndex(v => v.id == wfe_id && v.type == 2);
        Router.push({
          path: "/workflowDynamic",
          query: {
            access_token: GlobalData.requestToken
          }
          // query:{
          //   wfe_id:wfe_id
          // }
        })
      } else {
        console.log('审批动态');
        state.workflowPanes.push({
          title: "审批动态",
          id: wfe_id,
          type: 2,
          Panesindex: state.workflowPanes.length
        });

        Router.push({
          path: "/workflowDynamic",
          query: {
            access_token: GlobalData.requestToken
          }
          // query:{
          //   wfe_id:wfe_id
          // }
        })
        state.workflowActiveKey = state.workflowPanes.length - 1;
      }
      // dispatch('WorkflowRouterchange',wftId)

    },
    WorkflowRouterchange({ commit, state }, workflowActiveKey) {
      // workflowActiveKey 这个key 就是panes 的index
      console.warn('change');
      if (workflowActiveKey == 0) {
        state.workflowActiveKey = workflowActiveKey
      } else {
        console.log(state.workflowPanes[workflowActiveKey])
        if (state.workflowPanes[workflowActiveKey].type == 2) {
          state.workflowActiveKey = workflowActiveKey
        }

        if (state.workflowPanes[workflowActiveKey].type == 1) {
          state.workflowActiveKey = workflowActiveKey
        }
      }
    },
    Workflowremove({ commit, state }, targetKey) {
      console.log(targetKey);
      state.workflowPanes = state.workflowPanes.filter((v, i) => i != targetKey);
      state.workflowActiveKey = 0;
    }
  },
  mutations: {},
});
