import Vue from "vue";
import Router from "vue-router";
import GlobalData from '@/utils/Constant'
import { message } from 'ant-design-vue'
import getToken from "@/utils/token";
Vue.use(Router);

const routes = [
  {
    path: '/',
    name: 'list',
    meta: {
      title: '工作流列表页'
    },
    component: () => import("@/views/workflow/list.vue")
  },
  {
    path: '/edit',
    name: 'edit',
    meta: {
      title: '模板设计'
    },
    component: () => import("@/views/workflow/edit.vue")
  },
  {
    path: "/dynamicpage",
    name: "dynamicpage",
    meta: {
      title: '审批动态'
    },
    component: () => import("@/views/showflow/Dynamics.vue")
  },
  {
    path: "/todolist",
    name: "todolist",
    meta: {
      title: '待办列表'
    },
    component: () => import("@/views/showflow/Process.vue")
  },
  {
    path: "/tabs",
    name: "tabs",
    component: () => import("@/views/workflow/Tabs.vue")
  },
  {
    path: "/table",
    name: "table",
    component: () => import(/* webpackChunkName: "about" */ "@/views/showflow/Processtabs.vue"),
  }, {
    path: '/test',
    name: 'test',
    component: () => import('@/views/test.vue'),
  }, {
    path: '*',
    redirect: '/'
  }
]

const router = new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach(async (to, from, next) => {
  console.log('beforeEach');
  console.log(from.name, '-', to.name)
  document.title = to.meta.title || 'workflow'
  if (!GlobalData.requestToken && Reflect.has(to.query, 'access_token')) {
    if (!to.query.access_token)
      return message.error('缺少access_token')
    GlobalData.requestToken = to.query.access_token
  } else if (GlobalData.requestToken == 'test_token') {
    let token = await getToken()
    GlobalData.requestToken = token.data.data.access_token
  }
  next();
});

export default router